<template>
  <div class="statistics-page-wrapper">
    <tab pageType="vote"
          :count="joinCount"
          :joinMember="joinMember"
          @remind="remind"
          @getMoreHadJoin="getMoreHadJoin"
          @getMoreNotJoin="getMoreNotJoin"
    >
      <load-more slot="had-join" :over="!hadJoin.hasMore" :is-loading="hadJoin.isLoading"/>
      <load-more slot="not-join" :over="!notJoin.hasMore" :is-loading="notJoin.isLoading"/>
    </tab>
  </div>
</template>

<script type="ecmascript-6">
import { getVoteHadJoin, getVoteNotJoin, remindVote } from '@/api/vote';
import { AID, ERR_OK, LOGIN_EXPIRE } from '@/api/config';

import { mapGetters, mapMutations } from 'vuex';

import LoadMore from '@/base/load-more/load-more';
import Tab from '@/base/tab/join-member';

export default {
  data() {
    return {
      // had join loading
      hadJoin: {
        hasMore: true,
        isLoading: false,
        limit: 10,
        offset: 0
      },
      // not join loading
      notJoin: {
        hasMore: true,
        isLoading: false,
        limit: 10,
        offset: 0
      },
      // 参与成员列表
      joinMember: {
        hadJoinList: [],
        notJoinList: []
      }
    }
  },
  created() {
    if (!('canRemind' in this.joinCount)) {
      this.$router.push(`/vote/list/aid/${AID}/`);
    }
    this._getHadJoin();
    this._getNotJoin();
  },
  computed: {
    ...mapGetters(['joinCount'])
  },
  methods: {
    // 获取已参与成员
    _getHadJoin() {
      this.hadJoin.isLoading = true;

      const id = this.$route.params.id;
      const limit = this.hadJoin.limit;
      const offset = this.hadJoin.offset;

      getVoteHadJoin({ id, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.joinMember.hadJoinList.push(...res.data.data.mem_info);

          this.hadJoin.isLoading = false;

          if (res.data.data.mem_info.length < this.hadJoin.limit) {
            this.hadJoin.hasMore = false;
          }
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取未参与成员
    _getNotJoin() {
      this.notJoin.isLoading = true;

      const id = this.$route.params.id;
      const limit = this.notJoin.limit;
      const offset = this.notJoin.offset;

      getVoteNotJoin({ id, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.joinMember.notJoinList.push(...res.data.data.mem_info);

          this.notJoin.isLoading = false;

          if (res.data.data.mem_info.length < this.notJoin.limit) {
            this.notJoin.hasMore = false;
          }
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取更多已参与人员
    getMoreHadJoin() {
      if (!this.hadJoin.hasMore) {
        return
      }
      this.hadJoin.offset += 10;
      this._getHadJoin();
    },
    // 获取更多未参与人员
    getMoreNotJoin() {
      if (!this.notJoin.hasMore) {
        return
      }
      this.notJoin.offset += 10;
      this._getNotJoin();
    },
    // 提醒参与
    remind() {
      if (!this.joinCount.canRemind) {
        this.$dialog.toast({
            mes: '请稍后再来提醒吧！',
            timeout: 1500
        });
        return;
      }

      if (this.throttle) {
        return;
      }
      this.throttle = true;

      const voteId = this.$route.params.id;

      remindVote({ id: voteId }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.$dialog.alert({
            mes: `提醒成功`,
            timeout: 1500
          });

          this.setJoinCount(Object.assign({}, this.joinCount, { canRemind: false }));
        }

      }).catch((err) => {
        console.error(err);
        this.throttle = false;
      });
    },
    ...mapMutations({
      setJoinCount: 'SET_JOIN_COUNT'
    })
  },
  components: {
    LoadMore,
    Tab
  }
}
</script>

<style lang="stylus" scoped>
.statistics-page-wrapper
  position fixed
  top 0
  left 0
  right 0
  bottom 0
</style>




